* {
  box-sizing: border-box;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: 'Segoe UI', sans-serif;
  background: #000;
  color: white;
}

/* NAVBAR */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 15px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0,0,0,0.6);
  backdrop-filter: blur(10px);
  z-index: 1000;
}


.nav-links a {
  color: white;
  margin-left: 20px;
  text-decoration: none;
  transition: 0.3s;
}

.nav-links a:hover {
  color: #ff3c00;
}

/* HERO */
.hero {
  height: 100vh;
  background: url("https://images.unsplash.com/photo-1503376780353-7e6692767b70")
    center/cover no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 70px;
}

.overlay {
  background: rgba(0,0,0,0.75);
  padding: 60px;
  text-align: center;
  border-radius: 12px;
  animation: fadeIn 1.5s ease;
}

.overlay h1 {
  font-size: 72px;
  letter-spacing: 5px;
  font-weight: 700;
}

.overlay p {
  font-size: 18px;
  opacity: 0.85;
}


button {
  padding: 12px 25px;
  background: #ff3c00;
  border: none;
  color: white;
  cursor: pointer;
  margin-top: 20px;
}

/* SECTIONS */
.section {
  padding: 100px 40px;
  text-align: center;
}

.dark {
  background: #111;
}

/* GRID */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-top: 40px;
}

.card {
  background: #1a1a1a;
  padding: 30px;
  border-radius: 12px;
  transition: 0.4s ease;
  border: 1px solid rgba(255,255,255,0.05);
}

.card:hover {
  transform: translateY(-10px);
  background: #242424;
  border-color: #ff3c00;
}

.card h3 {
  margin-bottom: 10px;
  color: #ff3c00;
}


/* ANIMATION */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
